.header {
    display: flex;
    gap: 20px;
    border: 1px solid grey;
    padding: 20px;
    background-color: antiquewhite;
    cursor: pointer;
    margin-bottom: 20px;
  }
  .link {
    text-decoration: none;
    color: #000;
    font-size: 18px;
    font-weight: bold;
  }
  
  .link:global(.active) {
    color: red;
  }
  
  .error {
    color: red;
  }