.wrapBook {
    /* border: 1px solid rgba(16, 24, 40, 0.2); */
    border: 1px solid green;
    border-radius: 10px;
    padding: 24px;
    width: 448px;
    height: 532px; 
}

.title {
    font-weight: 600;
    font-size: 20px;
    line-height: 120%;
    color: #101828; 
    margin-bottom: 8px;
    margin-top: 0;

}

.text {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #475467; 
    margin-top: 0;
    margin-bottom: 24px;
}

.form {
    display: flex;
    flex-direction: column;
    gap: 14px;  
}

.label {
    position: relative;  
}

.input {
    /* width: 100%; */
    width: 400px;
/* height: 56px; */
padding: 18px 18px 18px 18px;
    background: transparent;
    border: 1px solid blue;
    /* border: transparent; */
    border-radius: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 125%;
    /* color: rgba(16, 24, 40, 0.6); */
    color: #101828; 
    /* opacity: 0.4;
    transition: opacity 0.3s ease;
    box-shadow: 0 4px 16px 0 rgba(22, 22, 22, 0.08);
   background: var(--inputBgColor); */
   
}

.textarea {
    border-radius: 10px;
    padding: 18px 18px 76px 18px;
    width: 400px;
    /* height: 114px; */
    border: 1px solid blue;
font-weight: 400;
font-size: 16px;
line-height: 125%;
/* color: rgba(16, 24, 40, 0.6); */
color: #101828;
margin-bottom: 10px;
}

.span {
    position: absolute;
    left: 0;
    bottom: -14px;
    font-size: 12px;
    color: red;  
}

label .inputPicker  {
    width: 400px;
    display: flex;
    align-items: center; 
    padding: 18px 18px 18px 18px;
    background: transparent;
    border: 1px solid blue;
    border-radius: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 125%;
    color: #101828; 
    flex-grow: 1;
}

.inputPicker svg {
    margin-left: 5px;
    cursor: pointer;
  }

